.map-loading {
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 9;
    background: hsla(0, 0%, 100%, 0.5);
}
.map-loading .st-loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 6px;
    background: #5191fa;
    border-radius: 5px;
    margin-left: -25px;
    -webkit-animation: st-load 1.8s ease-in-out infinite;
    animation: st-load 1.8s ease-in-out infinite;
}
.map-loading .st-loader:before {
    top: -20px;
    left: 10px;
    width: 40px;
    background: #fa5636;
    margin-left: -20px;
}
.map-loading .st-loader:after,
.map-loading .st-loader:before {
    position: absolute;
    display: block;
    content: "";
    -webkit-animation: st-load 1.8s ease-in-out infinite;
    animation: st-load 1.8s ease-in-out infinite;
    height: 6px;
    border-radius: 5px;
}
.map-loading .st-loader:after {
    bottom: -20px;
    width: 35px;
    background: #ffab53;
    margin-left: -17px;
}
@-webkit-keyframes st-load {
    0% {
        transform: translateX(40px);
    }
    50% {
        transform: translateX(-30px);
    }
    to {
        transform: translateX(40px);
    }
}
@keyframes st-load {
    0% {
        transform: translateX(40px);
    }
    50% {
        transform: translateX(-30px);
    }
    to {
        transform: translateX(40px);
    }
}
