    width: 40px;
    line-height: 32px;
    text-decoration: none;
    color: #5e6d77;
    float: none;
    padding: 0;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50% !important;
    transform: scale(1);
