.animateblock {
  padding: 8px 0;
  color: #fff;
  opacity: 0;
  -webkit-transition: all 0.4s linear;
  -moz-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.animateblock.animated1 {
  opacity: 1;
}
.animateblock.centerleft {
  display: block;
  width: auto;
  text-align: center;
  margin-right: -10%;
}
.animateblock.centerleft.animated1 {
  margin-right: 0;
}
.animateblock.centerright {
  display: block;
  width: auto;
  text-align: center;
  margin-left: -10%;
}
.animateblock.centerright.animated1 {
  margin-left: 0;
}
.animateblock.center {
  margin-left: 2%;
}
.animateblock.center.animated1 {
  margin-left: 42%;
}
.animateblock.size {
  display: block;
  width: 10%;
  text-align: center;
}
.animateblock.size.animated1 {
  width: 100%;
  height: 100%;
}
.animateblock.size img {
  max-width: 100%;
  height: auto;
}
